import React, { useEffect, useState } from 'react';
import Buttons from '../ActionButtons/Buttons';
import ExplainationBoard from '../ExplainationBoard/ExplainationBoard';
import Board from '../NumberBoard/Board';
import classNames from './Layout.module.css';
import Header from '../Header/Header';
import Keypad from '../Keypad/Keypad';

const Layout = () => {

    const [arrow, setArrow] = useState(<i className="fas fa-arrow-circle-left"></i>)
    const [toggleKeypad, setToggleKeypad] = useState(true)

    useEffect(()=>{
        document.getElementById('numberPad').classList.add(`${classNames.number_pad_container_grow}`)
        setArrow(<i className="fas fa-arrow-circle-right"></i>)
        setToggleKeypad(false)
    },[])

    const keypadToggle = () => {
        const numberPad = document.getElementById('numberPad');

        if(toggleKeypad){
            numberPad.classList.remove(`${classNames.number_pad_container_shrink}`)
            numberPad.classList.add(`${classNames.number_pad_container_grow}`);
            setArrow(<i className="fas fa-arrow-circle-right"></i>)
            setToggleKeypad(false)
        }else{
            numberPad.classList.remove(`${classNames.number_pad_container_grow}`);
            numberPad.classList.add(`${classNames.number_pad_container_shrink}`)
            setArrow(<i className="fas fa-arrow-circle-left"></i>)
            setToggleKeypad(true)
        }
    }

    return (
        <>
            <div className={classNames.container}>
                <Header />
                <div className={classNames.inner}>
                    <div className={classNames.board_container}>
                        <Board/>
                    </div>
                    <div className={classNames.explainer_container}>
                        <ExplainationBoard/>
                    </div>
                    <div className={`${classNames.number_pad_container}`} id="numberPad">
                        <div className={classNames.keypadTrigger}>
                            <button onClick={keypadToggle}>
                                {arrow}
                            </button>
                        </div>
                        <Keypad/>
                    </div>
                </div>
                <Buttons/>
            </div>
            <div className={classNames.mvlogo}>
                <img src='https://zoomcbd.com/math/logo.svg' alt='Logo' />
            </div>
            <div className={classNames.mvmenu}>
                <i className="fas fa-bars"></i>
            </div>
        </>
    );
};
export default Layout;